* {
    /*box-sizing: border-box;*/
}

/*Preventing the scrollbar to disappear in mobile apps*/
html,
body {
    height: 100%;
    min-height: 2000px;
}

/*End Preventing the scrollbar to disappear in mobile apps*/

body {
    user-select: none;
    background: radial-gradient(100% 225% at 100% 0%, #FF0000 0%, #000000 100%), linear-gradient(236deg, #00C2FF 0%, #000000 100%), linear-gradient(135deg, #CDFFEB 0%, #CDFFEB 36%, #009F9D 36%, #009F9D 60%, #07456F 60%, #07456F 67%, #0F0A3C 67%, #0F0A3C 100%);
    background-blend-mode: overlay, hard-light, normal;
    background-size: cover;
}

.text-center {
    text-align: center;
}

.font-weight-bold {
    font-weight: bolder;
}

/*text font*/
.text-rem-1 {font-size: 1rem}
.text-rem-1-2 {font-size: 1.2rem}
.text-rem-1-3 {font-size: 1.3rem}
.text-rem-1-4 {font-size: 1.4rem}
.text-rem-1-5 {font-size: 1.5rem}
.text-rem-1-6 {font-size: 1.6rem}
.text-rem-1-7 {font-size: 1.7rem}
.text-rem-1-8 {font-size: 1.8rem}
.text-rem-1-9 {font-size: 1.9rem}
.text-rem-2 {font-size: 2.0rem}
.text-rem-2-1 {font-size: 2.1rem}
.text-rem-2-2 {font-size: 2.2rem}
.text-rem-2-3 {font-size: 2.3rem}
.text-rem-2-4 {font-size: 2.4rem}
.text-rem-2-5 {font-size: 2.5rem}
.text-rem-3 {font-size: 3rem}
.text-rem-4 {font-size: 4rem}
.text-rem-5 {font-size: 5rem}

/*Letters border*/

.text-shadow { /*recommended*/
    text-shadow: 1px 1px 3px black;
}
.text-white {
    color: #ffffff;
}

.big-letters {
    font-size: 3rem;
}

.main-text p:nth-child(even), .big-letters {
    text-align: left;
    padding: 0 55px;
}

.main-text p:nth-child(odd) {
    text-align: right;
    padding: 0 55px;
}